html { font-size: 62.5%; } /* Adjustment to make 1rem=10px */

body {
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
  background-color: #4C566B; /* Avoids ugly white flashes between screens */
  overflow: hidden;
  height: 100%;
  width: 100%;
}

/* Font work */
h1, h2, h3, h4, h5, h6, p, div, ol, ul, li{
  font-family: 'Lato', sans-serif;
  color: rgba(210,204,204,1);
}



.react-phone-number-input__row > input {
	height: 5.3rem;
	border-radius: 9px;
	padding-left: 4.9rem;
	font-size: 2.1rem;
	margin-bottom: 1.5rem;
	/*color: #BFBFBF; */ /*Need to figure out how to change the color back to black when input starts*/
	background-image: url("/assets/mobile.png");
	background-repeat: no-repeat;
	background-size: 3.7rem;
	background-position-x: 0.8rem;
	background-position-y: center;
}

.rrui__select.rrui__select--collapsed.react-phone-number-input__country{
	display: none;
}


/* Styling Slick Carousel */

.slick-dots{
	top: 57vh !important;
	display: inline-table !important;/* Seems to make slick dots container equal the height of the contents */
}

.slick-dots li.slick-active button:before{
	color: #D2CCCC !important;
}

.slick-dots li button:before{
	font-size: 1rem !important;
	margin-left: 0.5rem !important;
}

/* To reinstate forward and back arrows, uncomment lines between here and /ARROWSTOP then comment out/delete display:none*/
.slick-arrow{
	display:none !important;
	/*margin: 0 9rem !important;*/
}

